<template>
    <div id="my-section">
      <!--全部订单-->
      <div class="my-order">
        <vbee-menu :nav="menu.myorder"></vbee-menu>
      </div>
      <!--我的资产-->
      <div class="my-assets">
        <vbee-menu :nav="menu.myassets"></vbee-menu>
      </div>
      <!--我的痕迹-->
      <div class="my-history">
        <vbee-menu :nav="menu.myhistory"></vbee-menu>
      </div>
      <!--我的vip-->
      <div class="my-vip">
        <vbee-menu :nav="menu.myvip"></vbee-menu>
      </div>
      <!--最后一排按钮-->
      <div class="my-last-menu">
        <vbee-menu :nav="menu.mylastmenu"></vbee-menu>
      </div>
    </div>
</template>

<script>
    import VbeeMenu from "./VbeeMenu";
    export default {
        name: "VbeeMSection",
      components: {VbeeMenu},
      props:["menu"]
    }
</script>

<style scoped>
  #my-section{
    background: #f3f4f6;
    flex: 1;
    overflow-x: hidden;
    overflow-y: auto;
    padding-bottom: 1.93rem;
  }
  /*去除滚动条*/
  ::-webkit-scrollbar {
    width: 0;
  }
  /*全部订单*/
  .my-order{
    height: .79rem;
    background: #fff;

  }
  #my-menu{
    height: .79rem;
  }
  #my-menu li a i{
    width: .3rem;
    height: .3rem;
    background: #f9f5ea;
  }

    /*我的资产*/
  .my-assets{
    margin-top:.09rem;
    height: .79rem;
    background: #fff;
    border-bottom: 2px solid #f5f5f5;
  }
   /*我的痕迹*/
  .my-history{
    height: .79rem;
    background: #fff;
  }
  /*我的vip*/
  .my-vip{
    margin-top:.09rem;
    height: .79rem;
    background: #fff;
    border-bottom: 2px solid #f5f5f5;
  }
  /*最后一排按钮*/
  .my-last-menu{
    height: .79rem;
    background: #fff;
  }
</style>



