<template>
    <div class="banner">
        <div class="ban">
          <div class="ban-left">
            <p class="moery">￥549</p>
            <p class="old">￥2199</p>
            <span class="zhe-ko">2.5折</span>
          </div>
          <div class="ban-right">
            <a href="javascript:0">
              <i class="iconfont icon-icon-test"></i>
            </a>
            <a href="javascript:0">
              <i class="iconfont icon-fenxiang"></i>
            </a>
          </div>
        </div>
        <div class="b-pname">
          <p>
            【热卖】天王沧海系列时尚大表盘男表百搭皮带石英男士手表
          </p>
        </div>
        <div class="b-youh">
          <div class="b-wph">
            <span class="b-type">唯品花</span>
            <p class="b-detail">开通唯品花首单立减20元</p>
            <a class="b-btn" href="javascript:0">领取</a>
          </div>
          <div class="b-wph">
            <span class="b-type b-type1">唯品币</span>
            <p class="b-detail">购买最多可获274个唯品币</p>
            <a class="b-btn" href="javascript:0"></a>
          </div>
        </div>
        <div class="b-color">
          <h5>颜色</h5>
          <div class="b-itco">
            <!---->
            <a v-for="(p,index) in stu" @click="addClass(index)" v-bind:class="{item1:index==currt}" class="b-item">
              <img :src="p.pic"/>
              <p>{{p.title}}</p>
            </a>
          </div>
        <h4>规格</h4>
        <div class="b-itco">
          <a @click="show()" class="b-item item1">
            <p>均码</p>
          </a>
        </div>
      </div>
        <vbee-pmask></vbee-pmask>
        <vbee-plis :kk="pp"></vbee-plis>
    </div>
</template>
<script>
    import VbeePlis from "./VbeePlis";
    import VbeePmask from "./VbeePmask";
    export default {
        name: "VbeePban",
      data(){
          return {
            "stu":[
              {"title":"黑色","pic":"./static/images/ph-1.jpg"},
              {"title":"玫瑰金","pic":"./static/images/ph-z1.jpg"},
              ],
            currt:0,
          }
      },
      props:["pp"],
      components: {VbeePmask, VbeePlis},
      methods:{
          addClass:function(index){
            this.currt = index;
          },
        }
    }
</script>

<style lang="scss" scoped>
 .banner{
   width:100%;
   height:4rem;
   .ban{
     padding:.1rem .05rem;
     box-sizing: border-box;
     display: flex;
     justify-content: space-between;
      .ban-left{
        display: flex;
        align-items: flex-end;
        .moery{
          font-size:.24rem;
          color:black;
        }
        .old{
          text-decoration: line-through;
          color:#98989f;
          font-size:.16rem;
          margin:0 .05rem;
        }
        .zhe-ko{
          font-size:.12rem;
          border-radius:.05rem;
          padding:.01rem 0.05rem;
          color:#fff;
          background:#39424a;
        }
      }
      .ban-right{
          .iconfont{
          color:#333333;
          font-size:22px;
          margin-right:.05rem;
         }
     }
   }
   .b-pname{
     margin:.15rem .05rem;
     font-size:.14rem;
     padding-bottom:.1rem;
   }
   .b-youh{
     border-top: 1px dotted #eaeaea;
     padding: .1rem;
     margin-bottom:.15rem;
     .b-wph{
       margin-top:.15rem;
       display: flex;
       justify-content:space-between;
       align-items: center;
       .b-type{
         border:1px solid #e4007f;
         padding: .01rem .1rem;
         line-height: .16rem;
         color:#e4007f;
       }
       .b-detail{
         font-size:.14rem;
         margin-left: -.8rem;
       }
       .b-type1{
         border:1px solid #5682c1;
         color:#5682c1;
       }
       .b-btn{
         width:.24rem;
        height:.16rem;
       }
     }
   }
   .b-color{
     border-top: 1px dotted #eaeaea;
     font-size:.24rem;
     padding:.1rem;
     margin-bottom: .15rem;
     h4{
       font-size:.16rem;
       margin-top:.15rem;
     }
     .b-itco{
       display: flex;
       .b-item{
         display: flex;
         width:30%;
         margin-right:.15rem;
         margin-top:.1rem;
         border:1px solid #ccc;
         color:black;
         font-size:.14rem;
         align-items: center;
         padding: .03rem 0;
         justify-content: center;
         img{
           width:.16rem;
           height:.16rem;
           margin:0 .05rem;
         }
       }
       .item1{
         border:1px solid #e4007f;
         color:#e4007f;
       }
     }
   }
 }

</style>



