<template>
    <div class="foo">
      <a class="foo-left" href="javascript:0">
        <router-link to="/cat">
          <i class="iconfont icon-gouwuche"></i>
          <span v-if="msg>0">{{msg}}</span>
        </router-link>
      </a>
      <a @click="add()" class="foo-right" href="javascript:0">加入购物车</a>
    </div>
</template>
<script>
    export default {
        name: "VbeePfoot",
        data(){
          return {
            "msg":0
          }
        },
        methods:{
          add(){
            this.msg++
          }
        }
    }
</script>

<style lang="scss" scoped>
.foo{
  width:100%;
  height:.66rem;
  background:#fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
  z-index: 1;
  .foo-left{
    width:.85rem;
    height:.42rem;
    border:1px solid #ccc;
    text-align: center;
    line-height: .42rem;
    color:black;
    font-weight: bolder;
    z-index: 0;
    position: relative;
    .iconfont{
      font-size:.22rem;
    }
    span{
      display: block;
      width:.2rem;
      height:.2rem;
      border-radius:50%;
      text-align: center;
      line-height: .2rem;
      background:#dd3d97;
      color:#fff;
      position: absolute;
      top:.01rem;
      right:.24rem;
    }
  }
  .foo-right{
     width:2.56rem;
     height:.44rem;
     background: #dd3d97;
     color:#fff;
     font-size:.14rem;
     line-height: .44rem;
     text-align: center;
   }
}
</style>



