<template>
    <div id="slide">
      <div class="swiper-container" id="slide1">
        <div class="swiper-wrapper">
          <a  class="swiper-slide" :key="index" v-for="(p,index) in lis">
            <img :src="p.pic" />
          </a>
        </div>
        <div class="swiper-pagination"></div>
      </div>
    </div>
</template>

<script>
  import  "../../../node_modules/swiper/dist/css/swiper.css"
  import Swiper from "../../../node_modules/swiper/dist/js/swiper"
    export default {
        name: "VbeePheader",
        props:["lis"],
      mounted(){
          new Swiper("#slide1",{
            autoplay:true,
            loop:true,
            autoplay:{
              disableOnInteraction:false
            },
            pagination:{
              el:".swiper-pagination",
              clickable:true
            }
          })
        }
    }
</script>

<style scoped>
  #slide{
    width:100%;
  }
  img{
    width:100%;
    height:3.75rem;
  }
</style>
<style>
  #slide .swiper-pagination .swiper-pagination-bullet {
    width:3px;
    height:3px;
    border-radius: 3px;
    background:#404040;
    opacity: 1;
    border: 1px solid #fff; }
  #slide .swiper-pagination .swiper-pagination-bullet-active {
    background: #fff; }
  #slide .swiper-pagination{
    bottom: .03rem;
  }
</style>



