<template>
  <div id="Vbee-login">
    <div id="user-header">
      <!--错号-->
      <a href="#">
        <router-link to="/my">
         <i class="iconfont icon-cuohao"></i>
        </router-link>
      </a>
      <!--中间的唯品会logo-->
      <img src="../../../static/images/loginLogo.png" alt="">
    </div>
    <!--下面的表单-->
    <div id="user-form">
      <div class="target-title">
        <p id="targetCon">账号名或密码错误，请重试</p>
      </div>
      <!--用户名-->
      <div class="user-name">
        <input type="text" id="username" placeholder="用户名 / 绑定手机 / 绑定邮箱">
      </div>
      <!--密码-->
      <div class="user-pass">
        <!--密码输入框-->
        <img id="demo_img" src="../../../static/images/eyeclose.png" @click="hideShowPsw()">
        <input type="password" id="demo_input" placeholder="请输入密码"/>
      </div>
      <!--忘记密码-->
      <div class="forget-pass">
        <a href="#">忘记密码</a>
      </div>
      <!--登录按钮-->
      <div class="user-btn">
        <input type="submit" value="登  录" @click="hasinfo">
      </div>
      <!--手机号快捷登录和新用户注册-->
      <div class="user-phone-register">
        <a href="#">手机号快捷登录</a>
        <a href="#">新用户注册</a>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "VbeeLogin",

    methods:{
      hideShowPsw:function () {
        // 这里使用最原始的js语法实现，可对应换成jquery语法进行逻辑控制
        var demoImg = document.getElementById("demo_img")
        var demoInput = document.getElementById("demo_input")

        //隐藏text block，显示password block
        if (demoInput.type == "password") {
          demoInput.type = "text";
          demoImg.src = "./static/images/eyeopen.png";
        }else {
          demoInput.type = "password";
          demoImg.src = "./static/images/eyeclose.png";
        }
      },

      // 点击登录按钮判断input框里是否有内容
      hasinfo:function(){
        var demoName = document.getElementById("username")
        var demoInput = document.getElementById("demo_input")
        var demoText = document.getElementById("targetCon")
        console.log(demoName.value)
        console.log(demoInput.value)
        if(demoName.value =="" || demoInput.value == ""){
          demoText.style.display = "block"
          demoText.innerHTML ="用户名和密码不能为空";
        }else if(demoName.onblur || demoInput.onblur){
          demoText.style.display = "none"
          demoText.innerHTML = ""
        }else{
          demoText.style.display = "none"
          demoText.innerHTML = ""
        }

      },
      //当input再次聚焦时 提示框取消

    }
  }
</script>

<style scoped>

  #user-header a .iconfont{
    color:#0e1010;
    font-size: .25rem;
    display: block;
    margin:.16rem .14rem;
  }
  #user-header img{
    width: 1.25rem;
    height: .5rem;
    display: block;
    margin:0 auto;
  }
  #user-form{
    padding:.42rem .18rem 0;
    display: flex;
    flex-direction: column;
  }
  /*用户名及密码错误提示框*/
  #user-form .target-title p{
    font-size: .1rem;
    color:#e32b1f;
    display: none;
  }
  /*用户名*/
  #user-form .user-name{
    border-bottom: 2px solid #d3d3d8;
  }
  /*用户密码*/
  #user-form .user-pass{
    border-bottom: 2px solid #d3d3d8;
    position: relative;
  }
  /*密码里的眼睛*/
  #demo_img{
    width: .15rem;
    height: .11rem;
    display: block;
    position: absolute;
    right: .14rem;
    top:.2rem;
  }
 /*form表单里的input样式*/
  #user-form input{
    width: 100%;
    height: .52rem;
    font-size: .13rem;
    color:#c3c2c3;
    border:none;
    outline: none;
    line-height: .52rem;
    display: flex;
    text-indent: .05rem;
  }
  /*忘记密码*/
  .forget-pass{
    display: flex;
    justify-content:flex-end;
    height: .32rem;
    align-items: center;
    padding-bottom: .09rem;
  }
  .forget-pass a{
    font-size: .1rem;
    color:#87888d;
  }
  /*登录按钮.*/
  .user-btn input{
    height: .4rem;
    background: #dd3d95;
    color:#f4add7;
    font-size: .12rem;
    display: flex;
    justify-content: center;
  }
  /*手机和新用户注册*/
  .user-phone-register{

    display: flex;
    justify-content: space-between;
    margin-top:.2rem;
  }
  .user-phone-register a{
    font-size: .11rem;
    color:#3883dc;
  }
</style>



