import Vue from 'vue'
import Router from 'vue-router'
import VbeeFication from  '../pages/VbeeFication'
import VbeeHunt from  '../pages/VbeeHunt'
import Index from '../pages/VbeeIndex'
import Cart from '../pages/VbeeCart'
import Inleft from '../pages/VbeeInleft'
import My from '../pages/VbeeMy'
import Prouts from '../pages/VbeeProuts'
import Login from '../components/vbeelogin/VbeeLogin'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/fica',
      name: 'VbeeFication',
      component: VbeeFication
    },
    {
      path: '/hunt',
      name: 'VbeeHunt',
      component: VbeeHunt
    },
    {
      path: '/cat',
      name: 'Cart',
      component: Cart
    },
    {
      path: '/inleft',
      name: 'Inleft',
      component: Inleft
    },
    {
      path: '/my',
      name: 'My',
      component:My
    },
    {
      path: '/prouts',
      name: 'Prouts',
      component:Prouts
    },
    {
      path:'/login',
      name:'Login',
      component:Login
    }
  ]
})



