<template>
    <div class="section">
      <vbee-ptop></vbee-ptop>
      <vbee-pheader :lis="imgs.slide"></vbee-pheader>
      <vbee-pban :pp="imgs"></vbee-pban>
    </div>
</template>

<script>
    import VbeePheader from "./VbeePheader";
    import VbeePban from "./VbeePban";
    import VbeePtop from "./VbeePtop";
    export default {
        name: "VbeePsection",
      props:["imgs"],
      components: { VbeePtop, VbeePban, VbeePheader}
    }
</script>

<style lang="scss" scoped>
  .section{
    width:100%;
    flex:1;
    overflow-x: hidden;
    overflow-y: auto;
    position: relative;
    z-index: 999;
  }
</style>



