import Vue from 'vue'
import App from './App'
import router from './router'
import resource from './resource'
import "./assets/css/common.css"
import { Button } from 'vant';

Vue.use(Button);
// vue.use(resource)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render(h) {
    return h(App)
  }
})



