<style lang="scss" scoped>
  .boy{
    width: 100%;
    padding-left: .08rem;
    box-sizing: border-box;
    margin-bottom: .19rem;
    overflow: hidden;
    display: flex;
    overflow-x: scroll;
    &::-webkit-scrollbar{
      height: 0;
    }
    li{
      width: 40%;
      border: 1px solid;
      padding: .05rem;
      box-sizing: border-box;
      margin-right: .1rem;
      flex-shrink: 0;
      img{
        width: 100%;
      }
      .name{
        font-size: .13rem;
        overflow : hidden;
        display: -webkit-box;
        text-overflow: ellipsis;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        margin-top: .06rem;
        color: #585c64;
      }
      .price{
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin: .02rem 0;
        .new{
          font-size: .14rem;
        }
        .old{
          font-size: .12rem;
          color: rgb(152, 152, 159);
          text-decoration: line-through;
        }
      }
      a{
        display: inline-block;
        width: 100%;
        height: .26rem;
        text-align: center;
        line-height: .26rem;
        background-color:  #dd3d97;
        border-radius: .26rem;
        color: #fff;
      }
    }
  }
</style>



