<template>
    <div class="cart">
      <div class="con">
        <header>
          <router-link to="/"><i class="iconfont icon-left-arrow"></i></router-link>
          <div class="carts">
            <div class="top">
              购物车
              <span>{{minute}}:{{second}}</span>
            </div>
            <p>配送至雁塔区</p>
          </div>
        </header>
        <div class="pro">
          <div class="title">精选特卖</div>
          <ul class="uls1">
            <li v-for="(p,index) in products">
              <div class="imgBox">
                <img :src="p.img" alt="">
              </div>
              <div class="dis">
                <div class="part">
                  <div class="left">
                    <h4 class="name">{{p.name}}</h4>
                    <p>{{p.type}}</p>
                    <p>{{p.contain}}</p>
                    <div class="count">
                      <span @click="minQal(index)">-</span>
                      <input type="number" v-model="p.num">
                      <span @click="addQal(index)">+</span>
                    </div>
                  </div>
                  <div class="right">
                    <div class="price">￥{{p.new}}</div>
                    <div class="old">￥{{p.old}}</div>
                    <i class="iconfont icon-cuohao"></i>
                  </div>
                </div>
              </div>
            </li>
          </ul>
          <div class="total">￥{{totalMoney}}</div>
          <div class="note">
            <span>运费</span>
            <p>唯品自营商品再购6元可享受新会员专享全场38元包邮</p>
          </div>
        </div>
        <ul class="list1">
          <li v-for="l in list">
            <span :style="l.color">{{l.left}}</span>
            <i :class="l.right"></i>
          </li>
        </ul>
        <cart-recommand></cart-recommand>
      </div>

      <div class="footer">
        <p>总金额 <span>￥{{totalMoney}}</span></p>
        <router-link to="#">结算</router-link>
      </div>
    </div>
</template>

<script>
    import CartRecommand from "../components/cart/CartRecommand";
    export default {
      name: "VbeeCart",
      components: {CartRecommand},
      data(){
        return{
          list:[
            {"left":"使用优惠券","right":"iconfont icon-you","color":"color:#5786ba"},
            {"left":"使用优惠口令","right":"iconfont icon-you","color":"color:#0d0d0d"},
          ],
          minutes:20,
          seconds:0,
          // 商品
          products:[
            {
              "img":"./static/images/pro1.jpg",
              "name":"【超值入门体验】蒂佳婷Dr.Jart+7天面膜套装 韩国美白补水保湿药丸面膜",
              "type":"明星同款护肤肌密",
              "contain":"均码",
              "num":"1",
              "new":"129",
              "old":"280"
            },
            {
              "img":"./static/images/pro2.jpg",
              "name":"唯品自营 | 【改造干皮星人】水润鲜活面膜礼盒 18片 补水细腻面膜套装",
              "type":"一叶子One Leaf护肤品专场",
              "contain":"18片",
              "num":"1",
              "new":"109",
              "old":"285"
            }
          ],
          totalNum:0,
          totalMoney:0
        }
      },
      created(){
        this._countCarts(this.products)
      },
      mounted(){
        this.add()
      },
      methods:{
        num:function(n){
          return n<10 ? "0" + n : "" + n
        },
        add:function () {
          var _this = this;
          var time = window.setInterval(function () {
            if (_this.seconds == "00" && _this.minutes != "00") {
              _this.seconds = 59;
              _this.minutes -= 1;
            }else if(_this.minutes == "00" && _this.seconds == "00"){
              window.close();
              window.clearInterval(time);
            }else{
              _this.seconds -= 1
            }

          },1000);
        },
        // 统计出总价格和总数量
        _countCarts(products) {
          this.totalNum = 0
          this.totalMoney = 0
          products.forEach((item,index)=>{
            this.totalNum += parseFloat(item.num)
            this.totalMoney += item.num * item.new
          })
        },
        // 点击增加件数
        addQal(i) {
          this.products[i].num++
        },
        // 点击减少件数
        minQal(i) {
          if( this.products[i].num <=1){
            this.products[i].num =1
          }else{
            this.products[i].num--
          }
        },
      },
      computed:{
        second:function () {
          return this.num(this.seconds)
        },
        minute:function () {
          return this.num(this.minutes)
        }
      },
      watch:{
        second:{
          handler(newVal){
            this.num(newVal)
          }
        },
        minute:{
          handler(newVal){
            this.num(newVal)
          }
        },
        //监听product中的数量发生变化，重新计算
        products:{
          handler:function (n,o) {
            this._countCarts(this.products)
          },
          deep:true
        },
      }
    }
</script>

<style lang="scss" scoped>
.cart{
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f3f4f6;
  .con{
    height: 100%;
    flex: 1;
    overflow-x: hidden;
    overflow-y: auto;
  }
  header{
    height: .4rem;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-bottom: .14rem;
    border-bottom: 1px solid #e8e8ea;
    .carts{
      .top{
        font-size: .15rem;
        span{
          font-size: .14rem;
          color: #b00c6d;
        }
      }
      p{
        text-align: center;
        font-size: .09rem;
        color: #a399a1;
      }
    }
    a{
      position: absolute;
      left: .12rem;
    }
  }
  .pro{
    padding: 0 .14rem;
    background-color: #fff;
    .title{
      height: .45rem;
      font-size: .12rem;
      line-height: .45rem;
      border-bottom: 1px solid #e6e6e6;
    }
    .uls1{
      li{
        padding-top: .18rem;
        padding-bottom: .25rem;
        display: flex;
        .imgBox{
          width: .63rem;
          height: .79rem;
          margin-right: .14rem;
          img{
            width: 100%;
            height: 100%;
          }
        }
        .dis{
          flex: 1;
          .part{
            font-size: .14rem;
            display: flex;
            .left{
              .name{
                display: -webkit-box;
                font-weight: 400;
                overflow: hidden;
                text-overflow: ellipsis;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
              }
              p{
                font-size: .1rem;
                line-height: .2rem;
                color: #999;
              }
              .count{
                color: #848891;
                input{
                  width: .315rem;
                  height: .21rem;
                  display: inline-block;
                  border: 1px solid #e7e7e7;
                  margin: 0 -.04rem;
                  text-align: center;
                }
                span{
                  text-align: center;
                  display: inline-block;
                  width: .24rem;
                  height: .21rem;
                  border: 1px solid #e7e7e7;
                }
              }
            }
            .right{
              text-align: right;
              margin-left: .28rem;
              .price{
                color: #222;
              }
              .old{
                font-size: .1rem;
                color: #98989f;
                text-decoration: line-through;
                margin-bottom: .44rem;
              }
              .iconfont{
                font-size: .18rem;
              }
            }
          }
        }
        &:last-child{
          border-bottom: 1px solid #e6e6e6;
        }
      }
    }
    .total{
      line-height: .44rem;
      font-size: .16rem;
      text-align: right;
    }
    .note{
      overflow: hidden;
      font-size: .12rem;
      color: #585c64;
      span{
        float: left;
        color: #3a5998;
        padding: 0 .02rem;
        border: 1px solid rgba(58,89,152,.5);
        margin-right: .06rem;
        margin-bottom: .24rem;
      }
    }
  }
  .list1{
    li{
      margin-top: .14rem;
      display: flex;
      padding-left: .14rem;
      padding-right: .06rem;
      justify-content: space-between;
      align-items: center;
      height: .44rem;
      font-size: .12rem;
      background-color: #fff;
      color: #868686;
      .iconfont{
        font-size: .2rem;
      }
    }

  }
  .footer{
    padding: 0 .13rem;
    font-size: .13rem;
    text-align: center;
    background-color: #fff;
    p{
      font-size: .12rem;
      line-height: .35rem;
      span{
        color: #dd0083;
      }
    }
    a{
      display: inline-block;
      width: 100%;
      line-height: .4rem;
      background-color: #dd0083;
      color: #fff;
      margin-bottom: .09rem;
    }
  }
}
</style>



