<template>
  <table>
    <tr v-for="p in tab">
      <td>{{p.title}}</td>
      <td>{{p.names}}</td>
    </tr>
  </table>
</template>

<script>
    export default {
        name: "VbeePtab",
      props:["tab"]
    }
</script>

<style lang="scss" scoped>

  table{
    width: 100%;
    padding:.1rem;
    box-sizing: border-box;
    font-size:.12rem;
    color:#5f636b;
  tr{
    width:100%;
    vertical-align: middle;
  td:nth-child(1){
    width:20%;
  }
  td:nth-child(2){
    height: auto;
  }
  }
  }
</style>



