<template>
    <div class="s-top">
      <a class="go-next" href="javascript:0">
        <router-link to="/hunt">
          <i class="iconfont icon-left-arrow"></i>
        </router-link>
      </a>
    </div>
</template>

<script>
    export default {
        name: "VbeePtop"
    }
</script>

<style lang="scss" scoped>
.s-top{
  width:.100%;
  padding:.1rem;
  box-sizing:border-box;
  position: absolute;
  top:0;
  z-index:9;
  .go-next{
    display: block;
    width:.42rem;
    height:.42rem;
    border-radius:50%;
    background-color:rgba(0,0,0,.5);
    color:#fff;
    text-align:center;
    line-height: .42rem;
  }
}
</style>



