<template>
    <div class="rec">
      <div class="title">实惠好货随手买</div>
      <div class="con">
        <div class="top">
          <a href="javascript:0" @click="toogleTabs(first)" :class="{active:first == this.flag}">男童上装</a>
          <a href="javascript:0" @click="toogleTabs(second)" :class="{active:second == this.flag}">女童上装</a>
          <a href="javascript:0" @click="toogleTabs(third)" :class="{active:third == this.flag}">女童套装</a>
          <a href="javascript:0" @click="toogleTabs(fourth)" :class="{active:fourth == this.flag}">女童下装</a>
        </div>
        <div class="box">
          <first :is="computView" keep-alive></first>
        </div>
      </div>
    </div>
</template>

<script>
    import first from "./First"
    import second from "./Second"
    import third from "./Third"
    import fourth from "./Fourth"
    export default {
      name: "CartRecommand",
      data(){
        return{
          first:"first",
          second:"second",
          third:"third",
          fourth:"fourth",
          computView:'first',
          flag:"-1"
        }
      },
      components:{
        first,
        second,
        third,
        fourth
      },
      methods:{
        toogleTabs (tabText){
          this.computView = tabText
          this.flag = tabText
        }
      },
      created(){
        this.flag = "first"
      }
    }
</script>

<style lang="scss" scoped>
.rec{
  margin: .14rem 0;
  background-color: #fff;
  .title{
    height: .5rem;
    font-size: .12rem;
    line-height: .5rem;
    padding-left: .14rem;
  }
  .con{
    font-size: .12rem;
    padding-left: .08rem;
    a{
      display: inline-block;
      color: #57585c;
      margin: 0 .15rem;
      padding-bottom: .07rem;
    }
    .active{
      color: #e8007e;
      border-bottom: .03rem solid #e8007e;
    }
    .box{
      display: flex;
      flex-wrap: nowrap;
      overflow-x: auto;
      margin-top: .19rem;
      &::-webkit-scrollbar{
        height: 0;
      }
      div{
        width: 30%;
        height: 1rem;
        flex-shrink: 0;
        margin-bottom: .19rem;
        img{
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
</style>



